import person1 from "../images/person-1.png";
import person2 from "../images/person-2.png";
import person3 from "../images/person-3.png";
import person4 from "../images/person-4.png";
import person5 from "../images/person-5.png";
import person6 from "../images/person-6.png";

const QuizSection = () => {
  return (
    <div className="quizSection">
      <h1 className="quizSection__title">
        There are 6 money personalities, which one are you?
      </h1>

      <div className="quizSection__persons">
        <div className="quizSection__unit">
          <img src={person1} alt="person" />
          <h2>The Idealist</h2>
          <p>
            You’re aware that money is a necessary part of life, but you’d
            rather not get bogged down with it.
          </p>
        </div>
        <div className="quizSection__unit">
          <img src={person2} alt="person" />
          <h2>The Stockpiler</h2>
          <p>You’re always saving for a rainy day, just in case.</p>
        </div>
        <div className="quizSection__unit">
          <img src={person3} alt="person" />
          <h2>The Hedonist</h2>
          <p>
            You know how to enjoy life. Money’s not going to get in the way of
            your freedom!
          </p>
        </div>
        <div className="quizSection__unit">
          <img src={person4} alt="person" />
          <h2>The Celebrity</h2>
          <p>
            Why settle for the ordinary when you can buy yourself the
            extraordinary?
          </p>
        </div>
        <div className="quizSection__unit">
          <img src={person5} alt="person" />
          <h2>The Nurturer</h2>
          <p>
            Your savings provide for the people you love if they ever find
            themselves in a pinch.
          </p>
        </div>
        <div className="quizSection__unit">
          <img src={person6} alt="person" />
          <h2>The Conqueror</h2>
          <p>
            Making money comes naturally to you because work is the love of your
            life.
          </p>
        </div>
      </div>

      <button>Find out more</button>
    </div>
  );
};

export default QuizSection;
