import Form from "./Form";
import playStore from "../images/playStore.png";
import appStore from "../images/appStore.svg";
import Mobile from "../images/headerMobile.png";

const Header = () => {
  return (
    <div className="header">
      <div className="content-container">
        <div className="header__text">
          <h1 className="heading-1">One card with all the right moves</h1>
          <p className="heading-2">
            Score Cash Advance up to $250* with no interest. Get paid up to 2
            days faster.^ Plus, rake in cashback rewards.†
          </p>
          <img src={Mobile} alt="Mobile Header" hidden />
          <div>
            <Form />
          </div>
          <small className="header__small">
            You're going to love getting carded.
          </small>
          <div className="header__icons">
            <img src={appStore} alt="ios logo" className="appstore" />
            <img src={playStore} alt="play store logo" className="playstore" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
