import { useState } from "react";

const Card1 = () => {
  const [toggle, setToggle] = useState(false);

  const handleClick = (e) => {
    setToggle(!toggle);
  };

  return (
    <div className={`card1 ${toggle && "card1--back"}`}>
      <h2 className={`card1__title ${toggle && "card1__title--back"}`}>
        {toggle
          ? "Strapped for cash?"
          : "Get Cash Advance up to $250 directly to your account"}
      </h2>
      {toggle && (
        <p className="card1__text">
          Life doesn't always wait for your next paycheck. Get Cash Advance up
          to $250 and repay automatically when you next get paid. No interest,
          no late fees, no risk to your credit.
        </p>
      )}
      <div>
        <button className="card1__btn btn" type="submit" onClick={handleClick}>
          {toggle ? "close" : "Read more"}
        </button>
      </div>
    </div>
  );
};

export default Card1;
