const Form = ({ grid }) => {
  return (
    <div className={`form ${grid === "yes" ? "form__grid" : ""}`}>
      <form action="">
        <input
          type="tel"
          placeholder="Enter your mobile number"
          name=""
          id=""
          className="form__input"
        />
        <button type="submit" className="form__btn btn" disabled>
          GET APP
        </button>
      </form>
    </div>
  );
};

export default Form;
