import Card2 from "./Card2";
import BG1 from "../images/home-tile-3.jpg";
import BG1Mob from "../images/home-tile-3-mobile.jpg";
import BG2 from "../images/home-tile-2.jpg";
import BG2Mob from "../images/home-tile-2-mobile.jpg";
import BG3 from "../images/home-tile-4.jpg";
import BG3Mob from "../images/home-tile-4-mobile.jpg";
import BG4 from "../images/home-tile-5.jpg";
import BG4Mob from "../images/home-tile-5-mobile.jpg";

const items = [
  {
    title: "Get your paycheck up to 2 days early",
    bgImg: BG1,
    bgImgMob: BG1Mob,
    bgColor: "#ccd3e7",
    titleBack: "Payday can't come fast enough?",
    backText:
      "Set up paycheck deposit to Empower, and we’ll make that money available to you ASAP. That means up to 2 days earlier than most banks. Total game changer.",
  },
  {
    title: "Get up to 10% cashback",
    bgImg: BG2,
    bgImgMob: BG2Mob,
    bgColor: "#efefef",
    titleBack: "Who doesn't love cashback?",
    backText:
      "No one, that's who. Rack up cashback of up to 10% on the places and things you spend money on already. No random offers, just great deals customized for you and your spending history.",
  },
  {
    title: "Score free unlimited access to 37,000 ATMs",
    bgImg: BG3,
    bgImgMob: BG3Mob,
    bgColor: "#fcd0ba",
    titleBack: "Tired of surprise ATM fees?",
    backText:
      "Get convenient access to over 37,000 ATM locations all across the US. Use them as often as you want and never get charged a single dime for taking out cash.",
  },
  {
    title: "Count on phone and live chat support",
    bgImg: BG4,
    bgImgMob: BG4Mob,
    bgColor: "#ccd3e7",
    titleBack: "Not a fan of email support?",
    backText:
      "We’ve got our A team on standby to handle your questions over the phone or live chat Monday-Friday from 6am-6pm PT. (We’ve got email too if that’s more your jam.)",
  },
];

const HomeFeedTwo = () => {
  return (
    <div className="homeFeedTwo">
      {items.map((item, idx) => (
        <Card2 items={item} key={idx} />
      ))}
    </div>
  );
};

export default HomeFeedTwo;
