import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Logo from "../images/headerLogo.svg";
import Form from "./Form";
const Navbar = () => {
  const [offset, setOffset] = useState(0);
  const [toggle, setToggle] = useState(false);

  let scrolled = offset > 50 ? "__scrolled" : "";

  useEffect(() => {
    window.onscroll = () => {
      setOffset(window.pageYOffset);
    };
  });

  return (
    <div className={`nav-container nav-container${scrolled}`}>
      <nav className={`navbar content-container`}>
        <img src={Logo} alt="Header logo" className="navbar__logo" />
        <button className="navbar__btn--hidden" hidden>
          GET APP
        </button>
        <button
          className="navbar__btn--toggle"
          hidden
          onClick={() => setToggle(!toggle)}
        >
          <span className="navbar__btn--icon"></span>
        </button>
        <ul className="navbar__ul">
          <li className="navbar__item">
            <Link to="/quiz" className="navbar__link">
              Quiz
            </Link>
          </li>
          <li className="navbar__item">
            <a href="/#" className="navbar__link navbar__link--disable">
              Support
            </a>
          </li>
          <li className="navbar__item navbar__item--last">
            <Form />
          </li>
        </ul>
      </nav>
      <ul className={`navbar__collapse ${!toggle && "navbar__collapse--hide"}`}>
        <li className="navbar__item--collapse">
          <Link to="/quiz" className="navbar__link">
            Quiz
          </Link>
        </li>
        <li className="navbar__item--collapse">
          <a href="/#" className="navbar__link navbar__link--disable">
            Support
          </a>
        </li>
      </ul>
    </div>
  );
};

export default Navbar;
