const DoubleCards = () => {
  return (
    <div className="doubleCards">
      <p className="doubleCards__title">Make your move</p>
      <h2 className="doubleCards__h2">Two colors, your choice</h2>
      <div className="doubleCards__grid">
        <div className="doubleCards__black">
          <h2>Black</h2>
        </div>
        <div className="doubleCards__peach">
          <h2>Peach</h2>
        </div>
      </div>
    </div>
  );
};

export default DoubleCards;
