import Form from "./Form";

const SignupPanel = () => {
  return (
    <div className="signupPanel">
      <h2>Download the Empower app for free today.</h2>
      <Form grid="yes" />

      <div className="signupPanel__btn" hidden>
        <button class="navbar__btn--hidden">GET APP</button>
      </div>
    </div>
  );
};

export default SignupPanel;
