import tile1 from "../images/company-1.png";
import tile2 from "../images/company-2.png";
import tile3 from "../images/company-3.png";
import tile4 from "../images/company-4.png";
import tile5 from "../images/company-5.png";
import tile6 from "../images/company-6.png";

const JumboLarge = () => {
  return (
    <div className="jumboLarge">
      <h2 className="jumboLarge__title">Loved by</h2>
      <div className="jumboLarge__grid">
        <img
          src={tile1}
          alt="company"
          className="jumboLarge__img jumboLarge__img--1"
        />
        <img
          src={tile2}
          alt="company"
          className="jumboLarge__img jumboLarge__img--2"
        />
        <img
          src={tile3}
          alt="company"
          className="jumboLarge__img jumboLarge__img--3"
        />
        <img
          src={tile4}
          alt="company"
          className="jumboLarge__img jumboLarge__img--4"
        />
        <img
          src={tile5}
          alt="company"
          className="jumboLarge__img jumboLarge__img--5"
        />
        <img
          src={tile6}
          alt="company"
          className="jumboLarge__img jumboLarge__img--6"
        />
      </div>
    </div>
  );
};

export default JumboLarge;
