import { Link } from "react-router-dom";
import Logo from "../images/quizLogo.svg";
import Landing from "../images/quizLanding.png";

const QuizHeader = () => {
  return (
    <div className="quizHeader">
      <div className="quizHeader__left">
        <Link to="/">
          <img src={Logo} alt="logo" />
        </Link>
        <h1 className="quizHeader__title">What's your money personality?</h1>
        <p className="quizHeader__text">
          Take the money psychology quiz to reveal your relationship with money.
          What makes you tick?
        </p>
        <button className="quizHeader__btn">Take the quiz</button>
        <small className="quizHeader__small">
          When clicking on this quiz, you agree to the Empower's Privacy Policy.
        </small>
      </div>

      <div className="quizHeader__right">
        <img src={Landing} alt="Quiz Landing" />
      </div>
    </div>
  );
};

export default QuizHeader;
