import { useState } from "react";
import Radium, { StyleRoot } from "radium";

const Card2 = ({ items }) => {
  const [toggle, setToggle] = useState(false);
  const { title, bgImg, bgImgMob, bgColor, titleBack, backText } = items;

  const handleClick = (e) => {
    setToggle(!toggle);
  };

  const stylesFront = {
    backgroundImage: `url(${bgImg})`,
    "@media (max-width: 660px)": {
      backgroundImage: `url(${bgImgMob})`,
    },
  };
  const stylesBack = {
    backgroundColor: bgColor,
  };

  return (
    <StyleRoot>
      <div className="card2" style={toggle ? stylesBack : stylesFront}>
        <h2 className="card2__title">{toggle ? titleBack : title}</h2>
        {toggle && <p className="card2__text">{backText}</p>}
        <div className={`card2__div ${toggle && "card2__div--back"}`}>
          <button
            className="card2__btn btn"
            type="submit"
            onClick={handleClick}
          >
            {toggle ? "close" : "Read more"}
          </button>
        </div>
      </div>
    </StyleRoot>
  );
};

export default Radium(Card2);
