import People from "../images//person-all.png";

const QuizAbout = () => {
  return (
    <div className="quizAbout">
      <h1 className="quizAbout__title">About the Money Psychology Quiz</h1>
      <div className="quizAbout__grid">
        <img src={People} alt="people" />
        <p className="quizAbout__p">
          This quiz was created by Empower. We made this quiz because we saw
          something missing in traditional finance advice. Well-meaning personal
          finance tips can be hard to follow because they’re purely rational –
          whereas our relationship with money is highly emotional. Our money
          behavior is deeply rooted in us, oftentimes formed in childhood.
        </p>
        <p className="quizAbout__p">
          The true first step to financial health is understanding your own
          relationship with money and what makes you tick. That’s why we created
          this quiz, backed by research from world-class psychologists and
          financial experts. Because at Empower, we believe financial advice
          should never be one-size-fits-all.
        </p>
      </div>
      <button>Start the quiz</button>
      <small>
        To develop the money personalities, Empower worked with Professor of
        Psychology Dr. Christina Villarreal, financial experts Tiffany Groode
        and Michelle Masters, author Heidi Priebe, and creative agency Wafffle.
      </small>
    </div>
  );
};

export default QuizAbout;
